<template>
  <q-card flat class="q-pa-sm rounded" style="width: 360px">
    <q-card-section>
      <q-toolbar class="bg-transparent q-pa-none">
        <CardIconBox icon="icon-folder-plus" />

        <div v-if="!noclose" class="absolute-top-right">
          <close-button />
        </div>
      </q-toolbar>
    </q-card-section>
    <q-card-section class="q-py-none">
      <div class="text-h5 font-demi-bold letter-space-normal q-pb-sm">
        Create New Folder
      </div>
      <div class="q-py-sm">
        <InputTextField
          autofocus
          v-model="folderName"
          placeholder="New Folder"
          class="q-pb-none"
          :rules="[(val) => foldername_pattern.test(val) || '']"
          @keyup.enter="submitChanges"
        />
      </div>
    </q-card-section>
    <q-card-actions align="right" class="full-width q-gutter-sm q-py-md">
      <q-btn
        no-caps
        outline
        unelevated
        class="col rounded button-gm-outline-dark"
        :disable="submitting"
        @click="$emit('close')"
        v-close-popup
      >
        <span class="text-body1 font-medium">Cancel</span>
      </q-btn>
      <q-btn
        no-caps
        unelevated
        color="primary"
        class="col rounded"
        :disable="submitting || !submitReady"
        @click="submitChanges"
      >
        <span class="text-body1 font-medium">Create</span>
      </q-btn>
    </q-card-actions>
  </q-card>
</template>

<script>
import { defineComponent } from 'vue';
import { mapActions } from 'pinia';
import useHelpers from 'stores/helpers';
import CardIconBox from 'src/components/Helpers/CardIconBox.vue';
import InputTextField from 'src/components/Helpers/InputTextField.vue';
import { extractErrorMessageString } from 'src/assets/scripts/functions.js';

export default defineComponent({
  name: 'CreateFolder',

  emits: ['close', 'success', 'failed'],

  components: { CardIconBox, InputTextField },

  props: {
    noclose: {
      type: Boolean,
      default: false,
    },
    parent_id: {
      type: Number,
      default: 0,
    },
  },

  data() {
    return {
      folderName: '',
      submitting: false,
      submitted: false,
      foldername_pattern: /^([a-zA-Z0-9 \(\)\._-]+){1}$/,
    };
  },

  mounted() {
    if (!this.parent_id) this.$emit('close');
  },

  computed: {
    submitReady() {
      return this.folderName
        ? this.foldername_pattern.test(this.folderName)
        : false;
    },
  },

  methods: {
    ...mapActions(useHelpers, ['createFolder']),

    async submitChanges() {
      if (!this.foldername_pattern.test(this.folderName)) return;
      this.submitting = true;
      if (!this.folderName) return;
      const response = await this.createFolder({
        name: this.folderName,
        parent_id: this.parent_id,
      });
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        this.$q.notify({
          icon: 'icon-check-circle-broken',
          message: message || 'Folder has been created',
        });
        this.submitted = true;
        this.$emit('success', response.data);
        this.$emit('close');
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Failed to create folder',
        });
        this.$emit('failed', message);
      }
      this.submitting = false;
    },
  },
});
</script>
